import { Question } from './question';

export class RadioQuestion extends Question<string> {
  controlType = 'radio';
  customStyle: string;
  options: {
    id: string,
    value: string,
    label: string
  }[] = [];

  constructor(options: object = {}) {
    super(options);
    this.customStyle = options['customStyle'] === 'checkbox' ?
      'custom-checkbox mt-2' :
      'custom-radio mt-1';
    this.options = options['options'] || [];
  }
}