import { Component, OnInit, OnDestroy } from '@angular/core';
import { Router } from '@angular/router';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { BsModalService, BsModalRef } from 'ngx-bootstrap/modal';
import { takeUntil } from 'rxjs/operators';
import { Subject } from 'rxjs';
import { StepperSelectionEvent } from '@angular/cdk/stepper';

import { WizardConfigService } from './wizard-config.service';
import { AddressVerificationComponent } from './address-verification/address-verification.component';
import { toFormQuestion, toFormGroup } from './wizard-helpers';

@Component({
  selector: 'coc-wizard',
  templateUrl: './wizard.component.html'
})
export class WizardComponent implements OnInit, OnDestroy {
  ngUnsubscribe: Subject<any> = new Subject();
  wizardConfig: any;
  step1FormGroup: FormGroup;
  step2FormGroup: FormGroup;
  step2BFormGroup: FormGroup;

  allQuestionGroups: any[];
  primaryQuestionGroups: any[];
  secondaryQuestionGroups: any[];

  bsModalRef: BsModalRef;

  stepperIndex: number = 0;
  isInChandler: boolean = false;
  step2BContinue: boolean = false;

  constructor(
    private router: Router,
    private fb: FormBuilder,
    private modalService: BsModalService,
    private wizardConfigService: WizardConfigService
  ) { }

  ngOnInit() {

    this.wizardConfigService.get()
      .pipe(takeUntil(this.ngUnsubscribe))
      .subscribe(
        (config) => {
          this.wizardConfig = config;
        },
        (error) => {
          console.error(error)
        },
        () => {

          this.allQuestionGroups = this.wizardConfig.questionGroups;
          
          // Convert array of questions in json config to Angular form groups
          this.allQuestionGroups.forEach(group => {
            var convertedQuestions = toFormQuestion(group.questions);
            group['formQuestions'] = convertedQuestions;
            var convertedFormGroup = toFormGroup(convertedQuestions);
            group['formGroup'] = convertedFormGroup;
          });

          // Create new array for primary questions from config
          this.primaryQuestionGroups = this.allQuestionGroups.filter(function(group) {
            return group.type === 'primaryGroup';
          });
          this.primaryQuestionGroups.sort((a, b) => a.order - b.order);

          // Create new array for secondary questions from config
          this.secondaryQuestionGroups = this.allQuestionGroups.filter(function(group) {
            return group.type === 'secondaryGroup';
          });
          this.secondaryQuestionGroups.sort((a, b) => a.order - b.order);

          // TODO: remove log
          console.log(this.primaryQuestionGroups, this.secondaryQuestionGroups);
        }
      );

    this.step1FormGroup = this.fb.group({
      alreadyRegistered: [null, Validators.required]
    });

    this.step2FormGroup = this.fb.group({
      addressLine1: ['', Validators.required],
      addressLine2: [null],
      addressCity: ['', Validators.required],
      addressState: ['', Validators.required],
      addressZip: ['', Validators.required]
    });
    
    // Simulating some validation to trigger address verification modal
    this.step2FormGroup.get('addressCity').valueChanges
      .pipe(takeUntil(this.ngUnsubscribe))
      .subscribe(value => {
        this.isInChandler = value.toLowerCase() === 'chandler';
      });

    this.step2BFormGroup = this.fb.group({
      solicitor: [false],
      specialEvent: [false],
      peddler: [false],
      registrationPrivileges: [false]
    });
    
    // Temporary validation to determine whether user should continue after step 2B
    this.step2BFormGroup.valueChanges
      .pipe(takeUntil(this.ngUnsubscribe))
      .subscribe(value => {
        this.step2BContinue = (
          value.solicitor === true ||
          value.solicitor === true ||
          value.specialEvent === true ||
          value.registrationPrivileges === true
        )
      });
  }

  ngOnDestroy() {
    // Subscription clean up
    // See https://stackoverflow.com/a/41177163
    this.ngUnsubscribe.next();
    this.ngUnsubscribe.complete();
  }

  handleStep2ContinueClick(): void {
    if (this.isInChandler) {
      this.openAddressVerificationModal();
    } else {
      this.stepperIndex = 2;
    }
  }

  openAddressVerificationModal(): void {
    // TODO: Replace temporary data with data from geocoder API
    let initialState = {
      'results': [
        {'address1': '123 W Park Lane', 'address2': '', 'city': 'Chandler', 'state': 'Arizona', 'zip': '85286'},
        {'address1': '123 W Park Ave', 'address2': '', 'city': 'Chandler', 'state': 'Arizona', 'zip': '85225'},
        {'address1': '123 W Park St', 'address2': '', 'city': 'Chandler', 'state': 'Arizona', 'zip': '85249'},
        {'address1': '123 Really Long Address Street', 'address2': 'Suite 700', 'city': 'Chandler', 'state': 'Arizona', 'zip': '85248'}
      ]
    };
    this.bsModalRef = this.modalService.show(AddressVerificationComponent, {
      initialState,
      class: 'modal-dialog-centered modal-lg',
      backdrop: 'static',
      keyboard: false
    });
    this.bsModalRef.content.selectedAddress
      .pipe(takeUntil(this.ngUnsubscribe))
      .subscribe(address => {
        // Update form values from suggested address in modal
        this.step2FormGroup.patchValue({
          addressLine1: address.address1,
          addressLine2: address.address2,
          addressCity: address.city,
          addressState: address.state,
          addressZip: address.zip
        });
      // Delay step change so modal closes first
      setTimeout(() => {
        this.stepperIndex = 3;
      }, 500);
    });
  }

  handleStep2BContinueClick(): void {
    if (this.step2BContinue) {
      this.stepperIndex = 3;
    } else {
      this.router.navigate(['/wizard/goodbye']);
    }
  }

  handleStepChange(event: StepperSelectionEvent): void {
    // Synchronize local step index with stepper component index
    this.stepperIndex = event.selectedIndex;
    // TODO: replace this scroll method with a global service
    var element = document.querySelector('body');
    setTimeout(() => {
      element.scrollIntoView({
        behavior: 'smooth',
        block: 'start',
        inline: 'nearest'
      });
    }, 50);
  }

}
