export class Question<T> {
  value: T;
  id: string;
  label: string;
  order: number;
  controlType: string;
  validation: object;

  constructor(options: {
      value?: T,
      id?: string,
      label?: string,
      order?: number,
      controlType?: string,
      validation?: object
    } = {}) {
    this.value = options.value;
    this.id = options.id || '';
    this.label = options.label || '';
    this.order = options.order === undefined ? 1 : options.order;
    this.controlType = options.controlType || '';
    this.validation = options.validation || {};
  }
}