import { FormGroup, FormControl } from '@angular/forms';

import {
  Question,
  TextboxQuestion,
  TextareaQuestion,
  CheckboxQuestion,
  RadioQuestion,
  DropdownQuestion
} from './question';

export function toFormQuestion(questions: any[]): Question<any>[] {
  let group: Question<any>[] = [];

  questions.forEach(question => {
    switch (question.controlType) {
      case 'textbox':
        group.push(
          new TextboxQuestion({
            id: question.id,
            label: question.label,
            placeholder: question.placeholder,
            order: question.order,
            type: question.type,
            validation: question.validation
          })
        );
        break;
      case 'textarea':
        group.push(
          new TextareaQuestion({
            id: question.id,
            label: question.label,
            placeholder: question.placeholder,
            order: question.order,
            rows: question.rows,
            validation: question.validation
          })
        );
        break;
      case 'checkbox':
        group.push(
          new CheckboxQuestion({
            id: question.id,
            label: question.label,
            placeholder: question.placeholder,
            order: question.order,
            customStyle: question.customStyle,
            validation: question.validation
          })
        );
        break;
      case 'radio':
        group.push(
          new RadioQuestion({
            id: question.id,
            label: question.label,
            order: question.order,
            customStyle: question.customStyle,
            options: question.options,
            validation: question.validation
          })
        );
        break;
      case 'dropdown':
        group.push(
          new DropdownQuestion({
            id: question.id,
            label: question.label,
            order: question.order,
            options: question.options,
            validation: question.validation
          })
        );
        break;
    }
  });

  return group.sort((a, b) => a.order - b.order);
}

export function toFormGroup(questions: Question<any>[]): FormGroup {
  let group: any = {};

  questions.forEach(question => {
    group[question.id] = new FormControl(question.value || '');
  });

  return new FormGroup(group);
}